module.exports = "import { HttpClient, HttpRequest, HttpResponse } from '@angular/common/http';\r\nimport { Component } from '@angular/core';\r\nimport { NzMessageService, UploadFile } from 'ng-zorro-antd';\r\nimport { filter } from 'rxjs/operators';\r\n\r\n@Component({\r\n  selector: 'nz-demo-upload-manually',\r\n  template: `\r\n  <nz-upload\r\n    [(nzFileList)]=\"fileList\"\r\n    [nzBeforeUpload]=\"beforeUpload\">\r\n    <button nz-button>\r\n      <i class=\"anticon anticon-upload\"></i><span>Select File</span>\r\n    </button>\r\n  </nz-upload>\r\n  <button nz-button [nzType]=\"'primary'\" [nzLoading]=\"uploading\" (click)=\"handleUpload()\" [disabled]=\"fileList.length == 0\" style=\"margin-top: 16px\">\r\n    {{ uploading ? 'Uploading' : 'Start Upload' }}\r\n  </button>\r\n  `\r\n})\r\nexport class NzDemoUploadManuallyComponent {\r\n  uploading = false;\r\n  fileList: UploadFile[] = [];\r\n\r\n  constructor(private http: HttpClient, private msg: NzMessageService) {}\r\n\r\n  beforeUpload = (file: UploadFile): boolean => {\r\n    this.fileList.push(file);\r\n    return false;\r\n  }\r\n\r\n  handleUpload(): void {\r\n    const formData = new FormData();\r\n    // tslint:disable-next-line:no-any\r\n    this.fileList.forEach((file: any) => {\r\n      formData.append('files[]', file);\r\n    });\r\n    this.uploading = true;\r\n    // You can use any AJAX library you like\r\n    const req = new HttpRequest('POST', 'https://jsonplaceholder.typicode.com/posts/', formData, {\r\n      // reportProgress: true\r\n    });\r\n    this.http\r\n      .request(req)\r\n      .pipe(filter(e => e instanceof HttpResponse))\r\n      .subscribe(\r\n        (event: {}) => {\r\n          this.uploading = false;\r\n          this.msg.success('upload successfully.');\r\n        },\r\n        err => {\r\n          this.uploading = false;\r\n          this.msg.error('upload failed.');\r\n        }\r\n      );\r\n  }\r\n}\r\n"