module.exports = "import { Component } from '@angular/core';\r\nimport { NzMessageService, UploadFile } from 'ng-zorro-antd';\r\n\r\n@Component({\r\n  selector: 'nz-demo-upload-drag',\r\n  template: `\r\n  <nz-upload\r\n    nzType=\"drag\"\r\n    [nzMultiple]=\"true\"\r\n    [nzLimit]=\"2\"\r\n    nzAction=\"https://jsonplaceholder.typicode.com/posts/\"\r\n    (nzChange)=\"handleChange($event)\">\r\n    <p class=\"ant-upload-drag-icon\">\r\n      <i class=\"anticon anticon-inbox\"></i>\r\n    </p>\r\n    <p class=\"ant-upload-text\">Click or drag file to this area to upload</p>\r\n    <p class=\"ant-upload-hint\">Support for a single or bulk upload. Strictly prohibit from uploading company data or other band files</p>\r\n  </nz-upload>\r\n  `,\r\n  styles: [\r\n    `\r\n  :host ::ng-deep nz-upload { display: block; }\r\n  :host ::ng-deep .ant-upload.ant-upload-drag { height: 180px; }\r\n  `\r\n  ]\r\n})\r\nexport class NzDemoUploadDragComponent {\r\n  constructor(private msg: NzMessageService) {}\r\n  // tslint:disable-next-line:typedef\r\n  handleChange({ file, fileList }): void {\r\n    const status = file.status;\r\n    if (status !== 'uploading') {\r\n      console.log(file, fileList);\r\n    }\r\n    if (status === 'done') {\r\n      this.msg.success(`${file.name} file uploaded successfully.`);\r\n    } else if (status === 'error') {\r\n      this.msg.error(`${file.name} file upload failed.`);\r\n    }\r\n  }\r\n}\r\n"