module.exports = "import { Component } from '@angular/core';\r\nimport { NzMessageService, UploadFile } from 'ng-zorro-antd';\r\n\r\n@Component({\r\n  selector: 'nz-demo-upload-avatar',\r\n  template: `\r\n  <nz-upload class=\"avatar-uploader\"\r\n    nzAction=\"https://jsonplaceholder.typicode.com/posts/\"\r\n    nzName=\"avatar\"\r\n    nzListType=\"picture-card\"\r\n    [nzShowUploadList]=\"false\"\r\n    [nzBeforeUpload]=\"beforeUpload\"\r\n    (nzChange)=\"handleChange($event)\">\r\n    <ng-container *ngIf=\"!avatarUrl\">\r\n      <i class=\"anticon anticon-plus\"></i>\r\n      <div class=\"ant-upload-text\">Upload</div>\r\n    </ng-container>\r\n    <img *ngIf=\"avatarUrl\" [src]=\"avatarUrl\" class=\"avatar\">\r\n  </nz-upload>\r\n  `,\r\n  styles: [\r\n    `\r\n    :host ::ng-deep .avatar-uploader > .ant-upload {\r\n      width: 128px;\r\n      height: 128px;\r\n    }\r\n    :host ::ng-deep .ant-upload-select-picture-card i {\r\n      font-size: 32px;\r\n      color: #999;\r\n    }\r\n    :host ::ng-deep .ant-upload-select-picture-card .ant-upload-text {\r\n      margin-top: 8px;\r\n      color: #666;\r\n    }\r\n  `\r\n  ]\r\n})\r\nexport class NzDemoUploadAvatarComponent {\r\n  loading = false;\r\n  avatarUrl: string;\r\n\r\n  constructor(private msg: NzMessageService) {}\r\n\r\n  beforeUpload = (file: File) => {\r\n    const isJPG = file.type === 'image/jpeg';\r\n    if (!isJPG) {\r\n      this.msg.error('You can only upload JPG file!');\r\n    }\r\n    const isLt2M = file.size / 1024 / 1024 < 2;\r\n    if (!isLt2M) {\r\n      this.msg.error('Image must smaller than 2MB!');\r\n    }\r\n    return isJPG && isLt2M;\r\n  }\r\n\r\n  private getBase64(img: File, callback: (img: {}) => void): void {\r\n    const reader = new FileReader();\r\n    reader.addEventListener('load', () => callback(reader.result));\r\n    reader.readAsDataURL(img);\r\n  }\r\n\r\n  handleChange(info: { file: UploadFile }): void {\r\n    if (info.file.status === 'uploading') {\r\n      this.loading = true;\r\n      return;\r\n    }\r\n    if (info.file.status === 'done') {\r\n      // Get this url from response in real world.\r\n      this.getBase64(info.file.originFileObj, (img: string) => {\r\n        this.loading = false;\r\n        this.avatarUrl = img;\r\n      });\r\n    }\r\n  }\r\n}\r\n"